
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { openAlbum, takePicture } from "../static/cordovaplugin.js";
import wechatAuth from './assets/js/wechatAuth'

const qs= require('qs');

export default {
name: "app",
data() {
return {
photos: [],
transitionName: "vux-pop-in",
showTabbar: true,
releaseShowState: false
};
},
created: function() {
switch (this.$route.path) {
case "/":
this.showTabbar = true;
break;
case "/page/UserIndex":
this.showTabbar = true;
break;

default:
this.showTabbar = false;
break;
}
},
created() {
},
computed: {},
methods: {
releaseShow() {
this.releaseShowState = true;
},
async takephoto() {
let photo = await takePicture();
this.photos.push(photo);
this.$router.push({
path: "/page/release/ReleaseImgEdit",
name: "ReleaseImgEdit",
params: {
photos: this.photos
}
});
this.releaseShowState = false;
},
async selectImg() {
let photos = await openAlbum();
this.$router.push({
path: "/page/release/ReleaseImgEdit",
name: "ReleaseImgEdit",
params: {
photos: this.photos.concat(photos)
}
});
this.releaseShowState = false;
}
},
watch: {
$route(to, from) {
switch (to.path) {
case "/":
this.showTabbar = true;
break;
case "/page/UserIndex":
this.showTabbar = true;
break;

default:
this.showTabbar = false;
break;
}
if (to.meta.index > from.meta.index) {
this.transitionName = "vux-pop-in";
} else {
this.transitionName = "vux-pop-out";
}
}
},
components: {
Tabbar,
TabbarItem,
Popup
}
};



