
import XHeader from 'vux/src/components/x-header/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { openAlbum, takePicture } from "../../../static/cordovaplugin.js";
import util from "../../util";

export default {
data() {
return {
popupState: false,
imgList: [
{
src: "static/img/icon/add-img.png"
}
],
editValue: ""
};
},
components: {
XHeader,
XTextarea,
XButton,
XImg,
Grid,
GridItem,
Popup
},
created: function() {},
methods: {

async takePhoto() {
let photo = await takePicture();
if (photo) {
this.imgList.push({
src: photo
});
}
this.popupState = false;
},

async selectAlbum() {
let photo = await openAlbum(1);
if (photo) {
this.imgList.push({
src: photo
});
await util.convertImg(photo, 100, 100);
}
this.popupState = false;
},

addImg() {
if (this.imgList.length > 5) {
this.$vux.alert.show({
title: '提示',
conbtent: '最多只能添加5张图片'
});
return false;
}
this.popupState = true;
},

commit() {

}
}
};



