
import XHeader from 'vux/src/components/x-header/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import util from "../../util";

export default {
data() {
return {
userInfo: {},
showState: false
};
},
components: {
XHeader,
Card,
XImg,
Grid,
GridItem
},
created: async function() {
let type = this.$route.query.type;
let userInfo = localStorage.getItem("userInfo");
if (type == "current") {
try {
this.userId = JSON.parse(localStorage.getItem("userInfo")).id;
} catch (data) {
this.userId = false;
}
if (this.userId) {
this.$vux.loading.show({
text: "Loading"
});
let data = await util.getData({
url: `/users/usersinfos?loginid=${this.userId}`,
method: "get"
});
data.usersinfos.logos =
data.usersinfos.logos || "static/img/default-avatar.png";
data.usersinfos.remarks = data.usersinfos.remarks || "当前大咖有点儿懒。。。";
this.userInfo = data;
/*
** 发布信息
*/
this.userInfo.imgList = [];
let imgList = await util.getData({
url: `/users/mypublishes?loginid=${this.userId}`,
method: "get"
});
imgList.forEach((item,index) => {
this.userInfo.imgList.push({
src: item.thumbs,
imgCount: item.image_total,
id: item.id
});
});
this.showState = true;
this.$vux.loading.hide();
}
}
},
methods: {}
};



