
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import util from '../../util';

export default {
data() {
return {
userInfo: null,
incomeRecordList: [
]
};
},
components: {
XHeader,
Flexbox,
FlexboxItem
},
created: async function() {
let userInfo = localStorage.getItem("userInfo");
if (userInfo) {
this.userInfo = JSON.parse(userInfo);
this.$vux.loading.show({
text: 'loading...'
});
let data = await util.getData({
url: `/users/myaccountsdetails?loginid=${this.userInfo.id}&pages=1&rowsmax=10`,
method: "get"
});
this.incomeRecordList = data;
this.incomeRecordList.forEach((item,index) => {
if(item.pays_types == 'alipay') {
item.iconType = "icon-zhifubao";
item.text = '支付宝支付';
}else {
item.iconType = "icon-weixin";
item.text = '微信支付';
}
});
console.log(this.incomeRecordList);
this.$vux.loading.hide();
}
}
};



